import React from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

const translateData = (t) => [
    { title: t("gold"), progress: "6.25" },
    { title: t("oil"), progress: "5.989583333333334" },
    { title: t("uk_equities"), progress: "34.895833333333336" },
    { title: t("hk_equities"), progress: "41.66666666666667" },
    { title: t("us_equities"), progress: "61.19791666666667" },
    { title: t("fine_wine"), progress: "88.02083333333334", selected: true },
    { title: t("gov_bonds"), progress: "88.02083333333334" },
    { title: t("corp_bonds"), progress: "94.53125" },
];

const points = [0, 0.5, 1, 1.5, 2, 2.5, 3];

const SharpeRatio = () => {
    const { t } = useTranslation(["liquidation"]);
    const data = translateData(t);
    return (
        <SharpeRatioWrapper>
            <h2>{t("sharpe_ratio_year", { startYear: 1988, endYear: 2018 })}</h2>
            <BarWrapper>
                <BarLinesWrapper>
                    {points.map((point) => (
                        <BarLine key={point} value={point.toFixed(2)} />
                    ))}
                </BarLinesWrapper>
                {data.map(({ title, progress, selected }) => (
                    <React.Fragment key={title}>
                        <BarTitle selected={selected}>{title}</BarTitle>
                        <Bar width={`${progress}%`} selected={selected} />
                    </React.Fragment>
                ))}
            </BarWrapper>
        </SharpeRatioWrapper>
    );
};

const SharpeRatioWrapper = styled.div`
    width: 100%;
    padding: 30px 53px 16px 39px;
    background: #fafafa;
    border: 1px solid #eeeeee;

    h2 {
        font-family: RoslindaleDisplayCondensed;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 32px;
        text-align: center;
        color: #112d47;
        margin-bottom: 30px;
    }

    @media screen and (max-width: 767px) {
        padding: 16.35px 28.85px 8.72px 21.255px;

        h2 {
            font-size: 13.08px;
            line-height: 17.44px;
            margin-bottom: 16.35px;
        }
    }
`;

const BarWrapper = styled.div`
    display: grid;
    grid-template-columns: 102px 1fr;
    gap: 12px 0px;
    grid-template-areas: ". .";
    align-items: center;
    position: relative;
    padding: 7px 1px 28px;

    @media screen and (max-width: 767px) {
        grid-template-columns: 55.59px 1fr;
        gap: 6.54px 0px;
        padding: 3.815px 1px 15.26px;
    }
`;

const BarTitle = styled.div`
    font-family: VinovestMono;
    font-style: normal;
    font-weight: 500;
    font-size: 11px;
    line-height: 16px;
    text-align: right;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #767a7f;
    ${({ selected }) => (selected ? "color: #242E35" : "")}
    margin-right: 22px;
    position: relative;

    @media screen and (max-width: 767px) {
        font-size: 5.995px;
        line-height: 8.72px;
        margin-right: 11.99px;
    }
`;
const Bar = styled.div`
    transition: 0.5s;
    background: #c5d5e4;
    ${({ width }) => (width ? `width: ${width}` : "width: 0")}
    ${({ selected }) => (selected ? "background: #242E35" : "")}
    height: 12px;
    position: relative;

    @media screen and (max-width: 767px) {
        height: 6.54px;
    }
`;

const BarLinesWrapper = styled.div`
    position: absolute;
    left: 102px;
    right: 0;
    bottom: 0;
    top: 0;
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 767px) {
        left: 55.59px;
    }
`;

const BarLine = styled.div`
    position: relative;
    height: 100%;
    width: 1px;

    &:before {
        position: absolute;
        top: 0;
        bottom: 20px;
        width: 100%;
        content: " ";
        background: #eeeeee;
        display: block;
    }

    &:after {
        position: absolute;
        bottom: 0;
        content: "${({ value }) => value}";
        transform: translateX(-50%);
        font-family: VinovestMono;
        font-style: normal;
        font-weight: 500;
        font-size: 11px;
        line-height: 16px;
        text-align: center;
        letter-spacing: 0.025em;
        text-transform: uppercase;
        color: #767a7f;
    }

    @media screen and (max-width: 767px) {
        &:before {
            bottom: 10.9px;
        }

        &:after {
            font-size: 5.995px;
            line-height: 8.72px;
        }
    }
`;

export default SharpeRatio;
